/** @jsxImportSource @emotion/react */

import { PropsWithChildren, useState } from 'react'
import { useTheme } from '@mui/material/styles'
import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import Carousel from 'react-material-ui-carousel'
import MobileStepper from '@mui/material/MobileStepper'

import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft'
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight'

import type { CarouselProps } from 'react-material-ui-carousel/dist/components/types'

export type CardCarouselProps = PropsWithChildren<
  CarouselProps & {
    dataLength: number
    show: number
  }
>

export function CardCarousel({
  children,
  dataLength,
  show,
  ...props
}: CardCarouselProps) {
  const theme = useTheme()

  const [activeStep, setActiveStep] = useState(0)

  const maxSteps = dataLength - show

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1)
  }

  const handlePrevious = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1)
  }

  const handleStepChange = (step: number) => {
    setActiveStep(step)
  }

  return (
    <>
      <Box sx={{ display: 'flex', flexDirection: 'row' }}>
        <Carousel
          next={handleNext}
          NextIcon={<KeyboardArrowRight />}
          prev={handlePrevious}
          PrevIcon={<KeyboardArrowLeft />}
          {...props}
        >
          {children}
        </Carousel>
      </Box>
      <MobileStepper
        variant="progress"
        steps={maxSteps}
        position="static"
        activeStep={activeStep}
        nextButton={
          <Button
            size="small"
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            Next
            {theme.direction === 'rtl' ? (
              <KeyboardArrowLeft />
            ) : (
              <KeyboardArrowRight />
            )}
          </Button>
        }
        backButton={
          <Button
            size="small"
            onClick={handlePrevious}
            disabled={activeStep === 0}
          >
            {theme.direction === 'rtl' ? (
              <KeyboardArrowRight />
            ) : (
              <KeyboardArrowLeft />
            )}
            Back
          </Button>
        }
      />
    </>
  )
}
